<template>
  <div id="instore" class="page">
    <!--头部-->
    <store-header></store-header>

    <ul class="nav one-bottom-px">
      <li v-for="item in storeItem" :class="{active: selectTab==item}" @click="select_Tab(item)"><span>{{item}}</span></li>
    </ul>

    <!--商品列表-->
    <store-goods v-show="selectTab=='商品'" :storeId ='storeId' ></store-goods>

    <!--评价-->
    <store-rating v-show="selectTab=='评价'"></store-rating>

    <!--店铺信息-->
    <store-info v-show="selectTab=='店铺'" :storeId ='storeId'></store-info>
  </div>
</template>

<script>
  import storeGoods from '../../components/msite/foodStore/store-goods.vue';
  import storeRating from '../../components/msite/foodStore/store-rating.vue';
  import storeInfo from '../../components/msite/foodStore/store-info.vue';
  import storeHeader from '../../components/msite/foodStore/store-header.vue';

  export default{
      data(){
          return{
              selectTab:'商品',
              storeItem:['商品','评价','店铺'],
              storeId : this.$route.params.id
          }
      },
    methods:{
      select_Tab(item){
          this.selectTab = item
    }
    },
    components:{
      'store-header':storeHeader,
      'store-goods':storeGoods,
      'store-rating':storeRating,
      'store-info':storeInfo,
    }
  }
</script>

<style>
  #instore{
    background: #fff;
  }
  #instore.page{
    bottom: 0;
  }
  #instore .nav{
    display: flex;
    height: 34px;
    background: #fff;
  }
  #instore .nav li{
    flex: 1;
    line-height: 34px;
    text-align: center;
    color: #666;
    font-size: 12px;
  }
  #instore .nav li.active{
    color: #0085FF;
  }
  #instore .nav li.active span{
    padding: 6px 0;
    border-bottom: 2px solid #0085FF;
  }

</style>



