<template>
  <div class="storehead" v-if="storeInfo.image_path" :style="{backgroundImage:'url('+ handlebgImg(storeInfo.image_path)+')'}">

      <div class="goback" @click="$router.back()"><</div>
      <div class="storecont" >
        <div class="storeimg">
          <img v-if='storeInfo.image_path' :src="handleImg(storeInfo.image_path)"/>
        </div>
        <div>
          <p class="storeName">{{storeInfo.name}}</p>
          <p class="storedeli">
            {{storeInfo.delivery_mode.text}}/
            {{storeInfo.order_lead_time}}分钟送达/
            配送费{{storeInfo.float_delivery_fee}}元
          </p>
          <p class="storeTips">公告:{{storeInfo.promotion_info}}</p>

        </div>

      </div>
    <div class="acti" v-if="storeInfo.activities.length>0">
      <div>
      <span :style="{background: '#'+storeInfo.activities[0].icon_color}" >{{storeInfo.activities[0].icon_name}}</span> <span>{{storeInfo.activities[0].description}}</span>
      </div>
      <div>
        <span @click="showBlock()">{{storeInfo.activities.length}}个活动></span>
      </div>
    </div>
    <transition enter-active-class="fadeIn myAnimated"
                leave-active-class="fadeOut myAnimated">
      <div class="block" v-if="showblock">

          <p class="storeName blockcenter">{{storeInfo.name}}</p>
          <p style="font-size:12px; color: #fff;" class="blockcenter">优惠信息</p>
          <p v-for="item in storeInfo.activities" class="actis"><span :style="{background: '#'+item.icon_color}">{{item.icon_name}}</span> {{item.description}}</p>
          <p style="font-size:12px; color: #fff;" class="blockcenter">商家公告</p>
          <div class="blocktips">公告:{{storeInfo.promotion_info}}</div>
          <div class="close" @click="hideBlock()">X</div>

      </div>
    </transition>
  </div>
</template>





<script>
  import {getStore , StoreInfo} from '../../../services/m-Request'
  import { handleImg , handlebgImg } from '../../../common/common.js'
  import Vuex from 'vuex'
  let blockSrcoll;
  export default{
      data(){
        return{
            storeInfo:{},
            showblock:0
        }
      },
      computed:{
        ...Vuex.mapState(['latitude','longitude'])
      },
      methods:{
          handleImg,
          handlebgImg,
          showBlock(){
              this.showblock = 1
          },
        hideBlock(){
              this.showblock= 0
        }
      },
      update(){
        blockSrcoll = new IScroll('.block',{

      })
      blockSrcoll.refresh();
      },
      created(){
          var storeid = this.$route.params.id;
          StoreInfo(storeid).then((result)=>{
            this.storeInfo = result;
          });
      }
  }
</script>

<style>
  .storehead{
    width:100%;
    height:160px;
    padding:0 14px;
    background-size: cover;
  }
  .storehead .goback{
    font-size:20px;
    color: #fff;
    margin-bottom:20px;
  }
  .storecont{
    display: flex;
  }
  .storehead .storeimg img{
    width:80px;
    height:80px;
    margin-right:10px;
  }
  .storeName{
    font-size:16px;
    color:#fff;
    margin-bottom: 6px;
    line-height:30px;
  }
  .storedeli{
    font-size:10px;
    margin-bottom: 6px;
    color: #fff;
  }
  .storeTips{
    width:250px;
    height:16px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    color: #fff;
    font-size:10px;
  }
  .acti{
    display: flex;
    justify-content: space-between;
  }
  .acti span{
    font-size:10px;
    color: #fff;
  }
  .block{
    width:100%;
    height:100%;
    position: absolute;
    left:0;
    top:0;
    bottom: 0;
    right:0;
    background: #262626;
    padding:40px 40px 70px 30px;
    overflow: auto;
    z-index: 10;
    border-bottom-width: 50px;
  }
  .block .actis{
    font-size:10px;
    color: #fff;
    line-height: 22px;
    margin-bottom:10px;
  }
  .blockcenter{
    text-align: center;
    margin-bottom:20px;
  }
  .blocktips{
    font-size:14px;
    color:#fff;
  }
  .close{
    width:40px;
    height:40px;
    line-height:40px;
    text-align: center;
    border:1px solid #eee;
    border-radius:50%;
    color: #fff;
    position: absolute;
    bottom:20px;
    left:50%;
    margin-left:-20px;
  }

</style>



