<template>
	<div class="home-banner">
		<div class="swiper-container">
		    <div class="swiper-wrapper">
		        <div class="swiper-slide" v-for="list in bannerList">	
			        <div class="banner-item" v-for="item in list">
						<img :src="handleImg(item.image_hash)" />
						<p>{{item.name}}</p>
					</div>                  
		        </div>
		    </div>
		    <div class="swiper-pagination"></div>
	</div>          
	</div>
</template>

<script>
	import { getEntries } from '../../services/m-Request.js'
	import Vuex from 'vuex'
	import { handleImg } from '../../common/common.js'
	var bannerSwiper;
	export default{
		data(){
			return{
				bannerList:[]
			}
		},
		methods:{
			handleImg
		},
		computed:{
			...Vuex.mapState(['latitude','longitude'])
		},
		created(){
			getEntries(this.latitude , this.longitude).then((result)=>{
				//这里需要将result数据分为两组
				while(result.length>0){
					let list = result.splice(0 , 8);
					this.bannerList.push(list);
				}
			})
		},
		mounted(){
		  	bannerSwiper = new Swiper ('.swiper-container', {
			    pagination: '.swiper-pagination'
			})  
		  },
		updated(){
		  		bannerSwiper.update();
		  }
	}
</script>

<style>
.home-banner .banner-item{
	width: 25%;
	float: left;
	margin-top: 10px;
}
.home-banner .banner-item img{
	width: 50%;
	margin: 0 auto;
}
.home-banner .banner-item p{
	text-align: center;
	font-size: 12px;
	color: #666;
}
.home-banner .swiper-wrapper{
	padding-bottom: 16px;
}
.home-banner .swiper-pagination{
	bottom: -4px;
}
.home-banner .swiper-pagination-bullet{
	width: 6px;
	height: 6px;
}
.home-banner .swiper-pagination-bullet.swiper-pagination-bullet-active{
	color: #000;
	opacity: 0.6;
	
}
</style>


