<template>
  <div class="storeGoods" id="stGoods">
    <nav class="nav-bar">
      <ul class="wrapper">
        <li
          v-for="(good ,index) in GoodsList"
          class="one-bottom-px"
          :class="{active: index == selectNavIndex}"
          @click="navAction(index)"
        >{{good.name}}</li>
      </ul>
    </nav>
    <div class="menu" >
      <div class="wrapper">
        <div class="menuList" v-for="good in GoodsList ">
          <p class="menuTitle one-bottom-px">{{good.name}} <span>{{good.des}}</span></p>
          <div class="menuGoods one-bottom-px" v-for="item in good.foods">
            <div class="g-img" v-if="item.image_path"><img :src="handleImg(item.image_path)"/></div>
            <div class="g-info">
              <dl>
                <dt>{{item.name}}</dt>
                <dd>
                  <div class="Godds-des">{{item.description}}</div>
                  <div class="goodsale">
                    <span>月售{{item.month_sales}}份</span> <span>好评率{{item.satisfy_rate}}%</span>
                  </div>
                  <div class="goods-pirce">
                    <span>￥{{item.specfoods[0].price}}<span v-if="item.specfoods.length>1" style="font-weight: normal;"> 起</span></span>
                    <span v-if="item.specfoods.length>1" class="choose">选规格</span>
                  </div>
                </dd>
              </dl>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { getStoreGoods } from '../../../services/m-Request'
  import { handleImg } from '../../../common/common.js'
  let menuScroll;
  let navScroll;
  export default{
      props:['storeId'],
      data(){
          return{
            selectNavIndex:0,
              GoodsList:[],
              menuHeightArr:[]

          }
      },
      methods:{
        handleImg,
        navAction(index){
            this.selectNavIndex = index;
            //点击左菜单 右菜单滚动到相应位置

          let height =0;
          for(let i=0;i<index;i++){
                height -= this.menuHeightArr[i];
            };
            menuScroll.refresh(); //刷新页面
            if(height>menuScroll.maxScrollY){
                menuScroll.scrollTo( 0 ,height)
            }
            else{
                menuScroll.scrollTo( 0 , menuScroll.maxScrollY)
            }
        }
      },
      created(){
        getStoreGoods(this.storeId).then((result)=>{
            this.GoodsList = result;
        });
      },
      mounted(){
          //  左菜单 滚动
          navScroll = new IScroll('.storeGoods .nav-bar',{
              bounce : false,
              click : true,
              tap:true
          });
          //左菜单 滚动 刷新
          navScroll.on('scrollStart',()=>{
            navScroll.refresh();
          });

          //右菜单 滚动
          menuScroll = new IScroll('.menu',{
            bounce : false,
            click : true,
            tap : true,
            probeType:3
          });

          //右菜单 滚动 刷新
          menuScroll.on('scrollStart',()=>{
            menuScroll.refresh();
            });

          menuScroll.on('scroll',()=>{
              let currentY =menuScroll.y;
              for (let i = 0; i< this.menuHeightArr.length;i++){
                  let min = 0;
                  for (let j = 0; j < i; j++){
                      min  -= this.menuHeightArr[j]
                  }
                  let max = min - this.menuHeightArr[i];
                  if(currentY <=min && currentY> max ){
                      this.selectNavIndex = i;
                      break;
                  }
              }

              //选中按钮
              let btns = Array.from(document.querySelectorAll('.nav-bar li'));
              let height = 0;
              for(let i=0;i<this.selectNavIndex;i++){
                  height -= btns[i].offsetHeight;
              }

              //判断菜单按钮是否可见
              if( navScroll.y> height && navScroll.y - navScroll.wrapper.offsetHeight <height ){

              }else if(height > navScroll.y){
                  navScroll.scrollTo( 0 ,height)
              }else if(height< navScroll.y - navScroll.wrapper.offsetHeight){
                  //还需要判断容器的最大滚动范围能不到这个按钮在顶部 如果不能 就到滚动的最大值
                if(height>navScroll.maxScrollY){
                    navScroll.scrollTo( 0, height)
                }else{
                    navScroll.scrollTo( 0, navScroll.maxScrollY)
                }
              }

          })

      },
      updated(){
          //获取菜单列表每一项高度
        let wrapperArr = Array.from(document.querySelectorAll('.menu .wrapper .menuList'));
        this.menuHeightArr = wrapperArr.map((wrapperDiv)=>{
            return wrapperDiv.offsetHeight
        })
      }
  }
</script>

<style>
.storeGoods{
  position: absolute;
  top: 194px;
  bottom: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  display: flex;
}
.nav-bar ul li{
  font-size:14px;
  padding:10px 8px;
  background: #f8f8f8;
  width:90px;
  color: #666;
  font-weight:normal;
  border-left: 2px solid #ededed;
}
#stGoods .nav-bar ul li.active{
  background: #fff;
  border-left: 2px solid #0085FF;
}
#stGoods .nav-bar li.one-bottom-px:after{
  bottom: 0px;
  /*border-bottom: 1px solid #666;*/
}
  .menu{
    width:100%;
    padding-left:10px;
  }
  .menu .menuTitle {
    width:100%;
    font-size:14px;
    font-weight:700;
    color: #666;
    line-height:26px;
    margin-bottom:20px;
  }
  .menu .menuGoods{
    display: flex;
    padding-bottom:20px;
  }
.menuList .menuTitle span{
    font-size:10px;
    font-weight:normal;
    color: #999;
  }
  .menu .g-img img{
    width:80px;
    height:80px;
    margin-right:10px;
  }
  .g-info dl dt{
    font-size:14px;
    font-weight:700;
  }
.g-info .Godds-des{
  width: 160px;
  font-size:12px;
  font-weight:normal;
  color: #999;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.g-info .goodsale span{
  font-size:10px;
  color:#666;
}
.goods-pirce{
  display: flex;
  justify-content: space-between;
}
.goods-pirce span{
  font-size:16px;
  color: #f60;
  font-weight:700;
}
.goods-pirce .choose{
  display: block;
  color: #fff;
  font-size:12px;
  background: #3199e8;
  border-radius: 5px;
  line-height:20px;
  padding:2px;
}
</style>



