<template>
  <div>
	<div id="msite" class="page">
		<div class="wrapper">
      <div class="top" :class="imgClass">
          <img :src="refreshImg" />
          <span class=reText>{{prompt}}</span>
      </div>
			<!--头部-->
			<m-head></m-head>

			<!--轮播图-->
			<m-banner></m-banner>

			<!--宣传-->
			<m-ad></m-ad>

			<!--商家列表-->
      <p class="listTitle">推荐商家</p>
			<m-list @update-Scoll = 'updateScoll' :list = 'List' @refresh-Scroll="refreshScroll"></m-list>

      <div class="bottom" v-if="isLoadMore">
        <img :src="loadImg" />
        <span class=reText>{{morePrompt}}</span>
      </div>
		</div>
    <div class="search" v-show="showSearch">
      <span class="searchtext">搜索商家、商品名称</span>
    </div>
	</div>

    <transition enter-active-class="slideInRight myAnimated"
                leave-active-class="slideOutRight myAnimated">
      <router-view></router-view>
    </transition>
  </div>
</template>



<script>
	import mHead from '../../components/msite/m-Head.vue';
	import mBanner from '../../components/msite/m-Banner.vue';
	import mAd from '../../components/msite/m-Ad.vue';
	import mList from '../../components/msite/m-List.vue';
  import { getStore } from '../../services/m-Request.js';
  import Vuex from 'vuex'
  let homeScroll;
	export default{
	    data(){
	      return{
	          refreshImg:'../../static/images/arrow.png',
            loadImg:'',
            showSearch:false,
            isLoadMore:false,
            imgClass:'',
            prompt:'下拉刷新',
            morePrompt:'加载更多',
            requestLimit:'20',
            List:[]
        }
      },
    computed:{
      ...Vuex.mapState(['latitude','longitude'])
    },
		components:{
			'm-head':mHead,
			'm-banner':mBanner,
			'm-ad':mAd,
			'm-list':mList
		},
    methods:{
	        refreshScroll(){  //从子级接受到的刷新指令
	            homeScroll.refresh();
          },
	        updateScoll(){
            homeScroll.refresh();
            setTimeout(()=>{
                var maxScroll = homeScroll.wrapper.offsetHeight - homeScroll.scroller.offsetHeight;
                if(maxScroll > homeScroll.y){
                    homeScroll.scrollTo(0 , maxScroll);
                }
            })
          },
	        requestData(flag){
            getStore(this.latitude , this.longitude , this.List.length, this.requestLimit).then((result)=>{
              if( flag == 'refresh'){
                this.List = [];
              }
              this.List = this.List.concat(result);
            });
          }
    },
    created(){
	        this.requestData();
	        this.$event.$on('update-data',()=>{
            this.requestData('refresh');
          })
    },
		mounted(){

      homeScroll = new IScroll('#msite',{
            probeType:2   //监听滚动行为
      });

			//设置初始位置
			homeScroll.scrollTo(0 , -40);

			//开始滑动
      homeScroll.on('scrollStart' , ()=>{
          if(homeScroll.y<0){
              this.isLoadMore = true
          }
        homeScroll.refresh();
        
			});

      

      //滑动中
      homeScroll.on('scroll',()=>{        
        //下滑中让搜索框显示
        if(homeScroll.y<= -80){
          this.showSearch = true
        }else{
          this.showSearch = false;
        };

        //刷新箭头方向判断
        if(homeScroll.y>=0){
          this.prompt = '松开刷新';
          this.imgClass = 'up'
        }else{
          this.prompt = '下拉刷新';
          this.imgClass = ''
        }


      });
      //滑动结束后
      homeScroll.on('scrollEnd',()=>{
          //判断滑动是否到临界点 不到临界点则自动返回
          if(homeScroll.y>-40 && homeScroll.y<0){
            homeScroll.scrollTo(0 , -40 ,300);
          }
          //判断判断滑动是否到临界点 到临界点则刷新
          if(homeScroll.y>= 0){
            this.prompt = '正在刷新';
            this.refreshImg = '../../static/images/ajax-loader.gif';
            // 刷新让 子组件进行
            this.$event.$emit('update-data');
            setTimeout(()=>{
              this.prompt = '松开刷新';
              this.refreshImg = '../../static/images/arrow.png';
              homeScroll.scrollTo(0 , -40 ,300);
            },1000)
          };

          //上拉加载更多
          let realY = homeScroll.maxScrollY + 40;
          if ( homeScroll.y >homeScroll.maxScrollY && homeScroll.y < realY){
            homeScroll.scrollTo(0 , realY ,300)
          }
          if(homeScroll.y <= homeScroll.maxScrollY ){
            this.morePrompt = '正在加载';
            this.loadImg = '../../static/images/ajax-loader.gif';
            this.requestData();
          }
      })
		}

	}
</script>

<style>
  #msite .wrapper .top , #msite .wrapper .bottom{
    width:100%;
    text-align: center;
    height:40px;
    line-height:40px;
  }
  .top img{
    width: 14px;
    height: 14px;
    display: inline-block;
    transform: rotate(180deg);
    transition: 300ms transform;
  }
  .bottom img{
    width: 14px;
    height: 14px;
    display: inline-block;
    transform: rotate(0deg);
    transition: 300ms transform;
  }
  .top.up img{
    transform: rotate(0deg);
  }
  .top .reText , .bottom .reText{
    font-size:16px;
  }
  #msite>.search{
    position: absolute;
    left:0;
    top:0;
  }
</style>



