<template>
  <div id="address" class="page">
    <div class="wrapper">
      <div class="addTitle">
        <span class="goback" @click="$router.back()"><</span><h1>选择收货地址</h1>
      </div>
      <div class="addwrapper">
          <div class="searchPart">
            <input type="text" placeholder="请输入地址" v-model="word"/>
          </div>
          <h4 v-if="showCurrent" class="currenTip">当前地址</h4>
          <div class="locas locaName" v-if="showCurrent">{{currentLoca}}</div>
          <div class="locas one-bottom-px" v-for="add in addList" @click="selectAction(add)">
            <span class="locaName">{{add.name}}</span>
            <span class="locaAdd">{{add.address}}</span>
          </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { searchAddress } from '../../services/m-Request';
  import { getAddress} from '../../services/m-Request';
  import Vuex from 'vuex';
  let addScroll;
  let Timer = null;
  export default{
      data(){
        return{
          currentLoca:'',
          word:'',
          addList : [],
          showCurrent:1
        }
      },
    computed:{
      ...Vuex.mapState(['latitude','longitude'])
    },
    methods:{
      selectAction(newAdd){
        console.log(newAdd);
        // 选择新地址之后传输给m-head
          this.$event.$emit('update-add' ,newAdd );
          // 传输到store仓库中
          this.$store.commit('modifyAdd',{
            lat: newAdd.lat,
            lon: newAdd.lon
          });
          // 返回首页
          this.$router.back();
        //刷新
        this.$event.$emit('update-data');
      },
      searchAction(){
        searchAddress(this.word ,this.latitude ,this.longitude )
          .then((result)=>{
            this.addList = result;
          })
      }
    },
    created(){
      getAddress(this.latitude , this.longitude).then((result)=>{
        console.log(result);
        this.currentLoca = result;
      });

      this.$watch('word', ()=>{

        clearTimeout(Timer);
        //请求
        Timer = setTimeout(()=>{
          this.showCurrent = 0;
          this.searchAction();
        }, 1000);
      })
    }
  }
</script>

<style>
  #address{
    background: #f4f4f4;
  }
  #address.page{
    bottom: 0;
  }
  #address .goback{
    position: absolute;
    left:5px;
    font-size:20px;
    color: #fff;
  }
  .addTitle{
    width:100%;
    height:40px;
    line-height:40px;
    background: #0085ff;
    text-align: center;
  }
  .addTitle h1{
    font-size:16px;
    color: #fff;
  }
  .searchPart{
    width:100%;
    height:40px;
    line-height:40px;
    text-align: center;
    background: #fff;
    position: relative;
  }
  .searchPart input{
    width:90%;
    height:30px;
    position: absolute;
    top:50%;
    left:50%;
    margin-top:-15px;
    margin-left:-45%;
    border: none;
    color: #999;
    background: #f2f2f2;
    padding-left:10px;
  }
  #address>h4{
    font-size:12px;
    color: #666;
    padding:16px;
  }
  .currenTip{
    font-size:10px;
    padding: 10px 0;
    margin-left:14px;
    font-weight:normal;
  }
  .locas{
    background: #fff;
    padding:5px 16px;
  }
  .locaName{
    color: #333;
    font-size:14px;
    font-weight:700;
    display: block;
  }
  .locaAdd{
    font-size:10px;
    font-weight:normal;
    color: #333;
  }
  .one-bottom-px:after{
    content: '';
    display: block;
    width: 100%;
    height: 1px;
    border-bottom: 1px solid #ccc;
    position: absolute;
    left: 0;
    bottom: -1px;
    transform: scaleY(0.5);
  }
</style>



