<template>
  <div class="StoreInfo">

    <div class="actis">
      <div class="infoTitle">活动与服务</div>
      <ul>
        <li v-for="item in storeInfo.activities">
          <p><span :style="{background: '#'+item.icon_color}" style="color: #fff; border-radius:2px;" >{{item.icon_name}}</span> {{item.description}}</p>
        </li>
        <p v-for="item in storeInfo.supports" class="s-support">
          <span :style="{background:'#'+item.icon_color}" style="color: #fff; border-radius: 2px;">{{item.icon_name}}</span>
          {{item.description}}
        </p>
      </ul>
    </div>
    <div class="info">
      <p style="font-size:16px; font-weight: 700; margin-bottom: 10px;">商家信息</p>
      <ul>
        <li class="one-bottom-px">{{storeInfo.name}}</li>
        <li class="storeTel one-bottom-px">
          <span style="font-weight: 700; color: #333">商家电话</span>
          <span>{{storeInfo.phone}}</span>
        </li>
        <li class="storeAdd one-bottom-px">
          <span style="font-weight: 700; color: #333">商家地址</span>
          <span>{{storeInfo.address}}</span>
        </li>
        <li class="openTime">
          <span style="font-weight: 700; color: #333">营业时间</span>
          <span v-for="item in storeInfo.opening_hours">{{item}}</span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  import {getStore , StoreInfo} from '../../../services/m-Request'
  export default{
      data(){
          return{
            storeInfo:{}
          }
      },
      created(){
        var storeid = this.$route.params.id;

        StoreInfo(storeid).then((result)=>{
          this.storeInfo = result;
        });
      }
  }
</script>

<style>
  .StoreInfo{
    position: absolute;
    top: 194px;
    bottom: 0;
    left: 0;
    width: 100%;
    overflow: auto;
    background: #f5f5f5;
  }
  .StoreInfo .actis .infoTitle{
    font-size:16px;
    font-weight:700;
    margin-bottom:10px;
  }
  .StoreInfo .actis{
    background: #fff;
    padding:10px 20px;
    margin-bottom:10px;
  }
  .StoreInfo .actis li{
    margin-bottom:6px;
  }
  .StoreInfo .actis li p{
    font-size:10px;
    color:#666;
  }
  .actis .s-support{
    font-size:10px;
    color:#666;
  }
  .StoreInfo .info{
    background: #fff;
    padding:10px 20px;
  }
  .StoreInfo .info ul li{
    font-size:14px;
    color: #666;
    padding:10px 0;
    display: flex;
    justify-content: space-between;
  }
  .storeAdd span:nth-child(2){
    text-align:right;
  }
</style>



