<template>
  <div id="search" class="page">
    <div class="searchPart">
      <span class="goback" @click="$router.back()"><</span>
      <input type="text" placeholder="请输入商品名称" v-model="word"/>
      <span class="schBtn" @click="searchBtn(word)">搜索</span>
    </div>
    <div class="hotSch">
      <p style="margin-bottom: 10px;">热门搜索</p>
      <span v-for="item in hotWordList" class="hotword">{{item}}</span>
    </div>

    <sch-Hotword v-if="showlist" @close="closeSearchList()"></sch-Hotword>
  </div>
</template>

<script>
  import hotword from './hotword.vue';
  import { getHot} from '../../services/m-Request';
  import Vuex from 'vuex'

  export default{
      data(){
        return{
            word:'',
            hotWordList:[],
            showlist:false
        }
      },
      methods:{
        searchBtn(){
            this.showlist = true;
            this.$route.params.word = this.word;
        },
        closeSearchList(){
          this.showlist = false
        }
      },
      components:{
          'sch-Hotword':hotword
      },
      computed:{
        ...Vuex.mapState(['latitude','longitude'])
      },
      created(){
        getHot(this.latitude , this.longitude).then((result)=>{
          this.hotWordList = result;
        })
      }
  }
</script>

<style>
  #search{
    background: #fff;
  }
  #search.page{
    bottom:0;
  }
  #search .searchPart{
    padding-top:14px;
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
  .searchPart .goback{
    font-size:20px;
  }
  .searchPart input{
    width:250px;
    height:30px;
    padding-left:15px;
    border:none;
    background: #f8f8f8;
  }
  .searchPart .schBtn{
    font-size:16px;
    font-weight:700;
    color: #333;
  }
  #search .hotSch{
    padding:14px;
  }
  .hotSch .hotword{
    display: inline-block;
    width:60px;
    font-size:14px;
    height: 28px;
    line-height:28px;
    text-align: center;
    background: #f7f7f7;
    color: #666;
    margin-right: 4px;
  }
</style>



