
import URL from './urlApi.js'

export function handleImg(path){
	if(path.endsWith('png')){
		path = path + '.png'
	}
	else if(path.endsWith('jpeg')){
		path = path + '.jpeg'
	}
	return URL.imgHost+path;
}

export function handlebgImg(path){
  if(path.endsWith('png')){
    path = path + '.png?imageMogr/format/webp/thumbnail/!40p/blur/50x40/'
  }
  else if(path.endsWith('jpeg')){
    path = path + '.jpeg?imageMogr/format/webp/thumbnail/!40p/blur/50x40/'
  }
  return URL.imgHost+path;
}



