import axios from 'axios';
import URL from '../common/urlApi.js';

//请求地址
export function getAddress(lat,lon){
	return new Promise ((resolve,reject)=>{
		axios.get(`${URL.addressApi}?latitude=${lat}&longitude=${lon}`)
		.then((response)=>{
			let address = response.data.address
			resolve(address)
		})
	})
};

//请求天气
export function getWeather(lat,lon){
	return new Promise ((resolve,reject)=>{
		axios.get(`${URL.weatherApi}?latitude=${lat}&longitude=${lon}`)
		.then((response)=>{
			let weatherInfo = response.data;
			resolve(weatherInfo);
		})
	})
};

//请求热搜
export function getHot(lat,lon){
	return new Promise ((resolve,reject)=>{
		axios.get(`${URL.hotApi}?latitude=${lat}&longitude=${lon}`)
		.then((response)=>{
			var list = response.data.map((itmes)=>{
				return itmes.word
			})
			resolve(list)
		})
	})
};

//请求轮播图
export function getEntries(lat,lon){
	return new Promise((resolve , reject)=>{
		axios.get(`${URL.entriesApi}?latitude=${lat}&longitude=${lon}&templates[]=main_template`)
		.then((response)=>{

			var list = response.data[0].entries.map((item)=>{
				var elist = {};
				elist.name = item.name;
				elist.image_hash = item.image_hash
				return elist		//return出去 list接收
			})
			resolve(list)
		})
	})
};

//请求商家
export function getStore(lat,lon,offset,limit){
	return new Promise((resolve,reject)=>{
		axios.get(`${URL.storeApi}?latitude=${lat}&longitude=${lon}&offset=${offset}&limit=${limit}&extras[]=activities&terminal=h5`)
		.then((response)=>{
      // console.log(response.data);
      var list = response.data.map((item)=>{
				var store = {};
				store.id = item.id;
				store.name = item.name;
				store.image_path = item.image_path;
        store.isbrand = item.is_premium;
        store.star = item.rating;
        store.orderNum = item.recent_order_num;
        store.minCost = item.float_minimum_order_amount;
        store.deliCost = item.float_delivery_fee;
        store.distance = item.distance;
        store.delitime = item.order_lead_time;
        store.activities = item.activities;
        store.showActivity = false;
				return store
			});
			resolve(list);
		})
	})
};

//地址搜索请求
export function searchAddress(word,lat,lon){
  return new Promise((resolve,reject)=>{
    axios.get(`${URL.searchAddressApi}?keyword=${word}&offset=0&limit=20&longitude=${lon}&latitude=${lat}`)
      .then((response)=>{

        let arr = response.data.map((item)=>{
          var addList = {};
          addList.lat = item.latitude;
          addList.lon = item.longitude;
          addList.name = item.name;
          addList.address = item.address;
          return addList
        });
        resolve(arr);

      })
  })
}

// 店铺名字信息类 请求
export function StoreInfo(id) {
  return new Promise((resolve,reject)=>{
    axios.get(`${URL.StoreInfoApi}${id}?extras[]=activities&extras[]=albums&extras[]=license&extras[]=identification&extras[]=qualification&terminal=h5&latitude=22.54286&longitude=114.059563`)
      .then((response)=>{
        let storeInfo = response.data;
        resolve(storeInfo);
      })
  })
}

//店铺商品请求
export function getStoreGoods(id) {
  return new Promise((resolve,reject)=>{
    axios.get(`${URL.StoreGoodsApi}?restaurant_id=${id}`)
      .then((response)=>{

        let arr = response.data.map((item)=>{
          let storeGoods = {};
          storeGoods.name = item.name;
          storeGoods.des = item.description;
          storeGoods.foods = item.foods;
          return storeGoods
        });
        resolve(arr);
      })
    }
  )
}

// 搜索商品
export function searchWord(word,lat,lon) {
  return new Promise((resolve,reject)=>{
    axios.get(`${URL.searchWordApi}?offset=0&limit=20&keyword=${word}&latitude=${lat}&longitude=${lon}&search_item_type=2&extra[]=activities`)
      .then((response)=>{

        //判断
        let data0 = response.data[0] || {};
        let arr0 = data0.restaurant_with_foods || [];
        let data1 = response.data[1] || {};
        let arr1 = data1.restaurant_with_foods || [];

        let data = [...arr0, ...arr1];
        let arr = data.map((item)=>{
          let store = {};
          store.id = item.restaurant.id;
          store.name = item.restaurant.name;
          store.image_path = item.restaurant.image_path;
          store.isbrand = item.restaurant.is_premium;
          store.star = item.restaurant.rating;
          store.orderNum = item.restaurant.recent_order_num;
          store.minCost = item.restaurant.float_minimum_order_amount;
          store.deliCost = item.restaurant.float_delivery_fee;
          store.distance = item.restaurant.distance;
          store.delitime = item.restaurant.order_lead_time;
          store.showActivity = false;
          return store
        });
        resolve(arr)
      })
  })
}










