<template>
	<div class="mlist">

		<ul class="seller-list">
		  <li class="seller-item one-bottom-px" v-for="item in list" @click="intoStore(item)">
        <img :src="handleImg(item.image_path)" />
        <div class="item-content ">
          <div class="brand-content">
            <span class="brand" v-if="item.isbrand == true">品牌</span>
            <h1 class="brandname">{{item.name}}</h1>
          </div>
          <div class="saleInfo">
            <span class="star smallText">{{item.star}}</span>
            <span v-if="item.orderNum" class="saleNum smallText">月售{{item.orderNum}}单</span>
          </div>
          <div class="deliver one-bottom-px">
            <div class="cost">
              <span class="minCost smallText">￥{{item.minCost}}起送</span>
              <span class="deliCost smallText">配送费￥{{item.deliCost}}</span>
            </div>
            <div class="range">
              <span class="dis deliText">{{item.distance}}m</span>
              <span class="time deliText">{{item.delitime}}分钟</span>
            </div>
          </div>
          <div class="activities" v-if="item.activities && item.activities.length>0">
            <span class="show-all" @click.stop="showActivity(item)">{{item.activities.length}}个活动</span>
            <p class="activity-item" v-for="(activity, index) in item.activities" v-if="index<2 || item.showActivity">
              <span :style="{background: '#'+activity.icon_color}">{{activity.icon_name}}</span>
              <span>{{activity.tips}}</span>
            </p>
          </div>

        </div>
      </li>
    </ul>
	</div>
</template>

<script>
	import Vuex from 'vuex'
	import { handleImg } from '../../common/common.js'

	export default{
	    props:{
        list:Array
      },
		data(){
			return{
				requestLimit:20
			}
		},
		computed:{
			...Vuex.mapState(['latitude','longitude'])
		},
		methods:{
			handleImg,
      // 展示活动
      showActivity(item){
        item.showActivity = !item.showActivity;
        this.$emit('update-Scoll');
      },
      //点击进入 外卖店铺
      intoStore(item){
          this.$router.push('/msite/instore/'+ item.id);
      }
		},
    updated(){
      this.$emit('refresh-Scroll');
    }
	}
</script>

<style>
	.mlist{
		padding: 10px;
	}
	.listTitle{
		font-size:16px;
		color:#333;
		font-weight: bold;
		padding-left: 10px;
	}

	.seller-list .seller-item{
	padding: 10px;
	display: flex;
}
.seller-list .seller-item>img{
	width: 60px;
	height: 60px;
}
.seller-list .seller-item .item-content{
	margin-left: 8px;
	flex: 1;
}
.brand-content{
  display: flex;
}
.brand{
  display: inline-block;
  font-size:12px;
  background: #fff100;
  color: #6f3f15;
  border-radius:5px;
  margin: 1px 5px 0 0;
  font-weight:700;
}
.seller-list .seller-item .item-content .brandname{
	width: 130px;
	font-size: 14px;
	font-weight: bold;
	color: #333;
	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
}
.deliver{
  display: flex;
  justify-content: space-between;
}
.smallText{
  color: #666;
  font-size:10px;
}
.deliText{
  color: #999;
  font-size:10px;
}
.deliCost:before{
  color: #ddd;
  content: "|";
  margin:0 3px;
}
.time:before{
  color: #ddd;
  content: "|";
  margin:0 3px;
}
.seller-list .activity-item{
    line-height: 20px;
    font-size: 12px;
    color: #666;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}
.seller-list .activity-item span:first-of-type{
    background: red;
    border-radius: 4px;
    color: #fff;
}
.seller-list .activity-item span:nth-of-type(2){
  width: 199px;
  display: inline-block;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 0.5em;
}
.seller-list .activities{
    position: relative;
}
.seller-list .activities .show-all{
    position: absolute;
    top: 4px;
    right: 4px;
    font-size: 12px;
    color: #999;
}
.deliver:after{
  content: '';
  display: block;
  width: 100%;
  height: 1px;
  border-bottom: 1px solid #ccc;
  position: absolute;
  left: 0;
  bottom: -1px;
  transform: scaleY(0.5);
}
</style>



