<template>
	<header class="mhead">
		<div class="locaInfo">
			<span class="loca" @click="goAddress()">{{address}}</span>
			<div class="weather">
				<span>{{weather.temperature}}</span>
				<span>{{weather.description}}</span>
				<img v-if="weather.image_hash" :src="handleImg(weather.image_hash)" />
			</div>
		</div>
		<div class="search">
			<span class="searchtext" @click="goSearch()">搜索商家、商品名称</span>
		</div>
		<div class="hotword">
			<span class="hot" v-for="words in hotWord" @click="goHotword(words)">
				{{words}}
			</span>
		</div>
	</header>
</template>

<script>
	import {getAddress , getWeather , getHot} from '../../services/m-Request.js'
	import Vuex from 'vuex'
	import { handleImg } from '../../common/common.js'

	export default{
		data(){
			return{
				address:'',
				weather:{},
				hotWord:[]
			}
		},
		computed:{
			...Vuex.mapState(['latitude','longitude'])
		},
		methods:{
			handleImg,
      goAddress(){
			    this.$router.push('/msite/address');
      },
      goSearch(){
        this.$router.push('/msite/search');
      },
      goHotword(word){
        this.$router.push('/msite/hotword/'+word);
      },
      requestData(){
        getAddress(this.latitude,this.longitude).then((result)=>{
          this.address = result
        });
        getWeather(this.latitude,this.longitude).then((result)=>{
            this.weather = result
        });
        getHot(this.latitude,this.longitude).then((result)=>{
            this.hotWord = result
        })
      }
		},
		created(){
      this.requestData();

      this.$event.$on('update-add',(result)=>{
        this.address = result.name;
        console.log(this.address);
      });

      this.$event.$on('update-data',()=>{
        this.requestData();
      })


		}
	}
</script>

<style>
	.mhead{
		background: #0085ff;
	}
	.locaInfo{
		width: 100%;
		height: 40px;
		background: #0085ff;
		position: relative;
	}
	.loca{
		font-size:15px;
		color: #fff;
		font-weight: bold;
		position: absolute;
		left: 10px;
		top: 10px;
		max-width: 135px;
	    white-space: nowrap;
	    overflow: hidden;
	    text-overflow: ellipsis;
	}
	.locaInfo .weather{
		position: absolute;
		top: 5px;
		right: 10px;
		height: 30px;
		color: #fff;
}
	.locaInfo .weather span{
		position: absolute;
		right: 30px;
		top: 0;
		font-size: 12px;
		width: 40px;
		text-align: center;
	}
	.locaInfo .weather span:last-of-type{
		top: 15px;
	}
	.locaInfo .weather img{
		position: absolute;
		width: 30px;
		top: 0;
		right: 0;
	}
	.search{
    width: 100%;
		height: 50px;
		background: #0085ff;
		padding: 10px 14px;
	}
	.searchtext{
		display: block;
		width: 100%;
		height: 30px;
		background: #FFF;
		line-height: 30px;
		font-size:14px;
		color: #666;
		font-weight: bold;
		text-align: center;
	}
	.hotword{
		box-sizing: border-box;
		width: 100%;
		height: 30px;
		padding: 0 10px;
		overflow-x: auto;
		white-space: nowrap;
}
	.hotword .hot{
		margin: 0 10px;
		font-size: 12px;
		color: #fff;
		line-height: 30px;
	}
</style>



