import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)


import Msite from '../pages/msite/msite.vue'
import Address from '../pages/msite/address.vue'
import Search from '../pages/msite/search.vue'
import Hotword from '../pages/msite/hotword.vue'
import inStore from '../pages/msite/inStore.vue'
import Discover from '../pages/discover/discover.vue'
import Order from '../pages/order/order.vue'
import Profile from '../pages/profile/profile.vue'


export default new Router({
  routes: [
  	{
  		path: '/msite',
  		component: Msite,
      children:[
        {
          path:'address',
          component:Address
        },
        {
          path:'search',
          component:Search
        },
        {
          path:'hotword/:word',
          component:Hotword
        },
        {
          path:'instore/:id',
          component:inStore
        }
      ]
  	},
  	{
  		path: '/discover',
  		component: Discover
  	},
  	{
  		path: '/order',
  		component: Order
  	},
  	{
  		path: '/profile',
  		component: Profile
  	},
  	{
  		path:'/',
  		redirect:'/msite'
  	}
  ]
})



