<template>
  <transition enter-active-class="slideInRight"
              leave-active-class="slideOutRight">
    <div id="hotword" class="page">
      <div class="hw-top">
        <span class="goback" @click="goBack()"><</span>
        <span class="hw-search">
          <input type="text" v-model="keyword"/>
        </span>
        <span class="confirm" @click="searchBtn()">搜索</span>
      </div>
      <ul class="tab one-bottom-px">
        <li v-for="item in tabList">{{item}}</li>
      </ul>
      <m-list :list="goodsList"></m-list>
    </div>
  </transition>
</template>

<script>
  import mlist from '../../components/msite/m-List.vue';
  import { searchWord } from  '../../services/m-Request';
  import Vuex from 'vuex';
  export default{
      data(){
          return{
              tabList:['分类','排序','筛选'],
              keyword:this.$route.params.word,
              schWord:'',
              goodsList:[]
          }
      },
      methods:{
        goBack(){
            //从搜索栏进来 返回搜索栏
            if(this.$route.path == '/msite/search'){
                this.$emit('close');
            }
            else if(this.$route.path == '/msite/hotword/'+this.keyword){
              this.$router.push('/msite');
            }
            else{
                //从首页进来直接返回
              this.$router.back()
            }
        },
        searchBtn(){
            //搜素文字设置
          this.$router.push('/msite/hotword/'+this.keyword);
            // 刷新
          this.requestDate();
        },
        requestDate(){
          searchWord(this.keyword , this.latitude , this.longitude).then((result)=>{
            this.goodsList = result;
          });
        }
      },
      computed:{
        ...Vuex.mapState(['latitude', 'longitude'])
      },
      components:{
          'm-list':mlist
      },
      created(){
        this.requestDate();
      }
  }
</script>

<style>
  #hotword.page{
    bottom:0;
  }
  #hotword{
    background: #fff;
  }
  .hw-top{
    padding-top:10px;
    display: flex;
    justify-content: space-around;
  }
  .hw-top .goback{
    font-size:20px;
  }
  .hw-top .hw-search input{
    width:250px;
    height:30px;
    padding-left:15px;
    border:none;
    background: #f8f8f8;
  }
  .hw-top .confirm{
    font-size:16px;
    font-weight:700;
  }
  #hotword .tab{
    display: flex;
    justify-content: space-around;
    margin-top: 10px;
    padding-bottom:10px;
  }
</style>



