import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);

const state = {
	latitude: 22.54286,
	longitude: 114.059563
};
const mutations = {
  modifyAdd(st,parma){

    st.latitude = parma.lat;
    st.longitude = parma.lon;
  }
};


export default new Vuex.Store({
	state,
  mutations
})



