<template>
  <div id="app">
    <keep-alive>
    <router-view></router-view>
  	</keep-alive>

  	<nav class="tabs one-top-px">
  		<router-link class="tab" v-for="tab in tablist" :to="tab.path">
  			<span class="tabicon" :class="tab.icon"></span>
  			<em class="title">{{tab.title}}</em>
  		</router-link>
  	</nav>
  </div>
</template>

<script>
export default {
	data(){
		return {
				tablist: [
					{title: '外卖', path: '/msite', icon: 'icon-eleme'},
					{title: '发现', path: '/discover', icon: 'icon-discover'},
					{title: '订单', path: '/order', icon: 'icon-order'},
					{title: '我的', path: '/profile', icon: 'icon-me'}
			  ]
		}
	}
}
</script>

<style>
#app {
  width: 100%;
  height: 100%;
  background: #fff;
}
.tabs{
	width: 100%;
	height: 49px;
	position: absolute!important;
	bottom: 0;
	left: 0;
	display: flex;
}
.tabs .tab{
	flex: 1;
}
.tabs .tab .tabicon{
	display: block;
	width: 100%;
	height: 30px;
	text-align: center;
	line-height: 36px;
	color: #666;
}
.tabs .tab.router-link-active .tabicon{
	color: #0089dc;
}
.tabs .tab .title{
	display: block;
	width: 100%;
	height: 24px;
	text-align: center;
	font-size: 12px;
	font-style: normal;
	color: #666;
}
.tabs .tab.router-link-active .title{
	color: #0089dc;
}
.page{
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 49px;
  z-index: 10;
  overflow: hidden;
}
</style>



